<script setup>
import { ref, defineEmits } from 'vue';
    const emits = defineEmits(['emitSearchText'])
    
    const searchText = ref('')
    const emitText = (e) => {
        if (window.event.keyCode == 13 || e) {
            emits('emitSearchText', searchText.value)
        }
    }
</script>
<template>
    <div>
        <input type="text" @keypress="emitText()" v-model="searchText" placeholder="국가명을 입력해주세요.">
        <button @click="emitText(true)">
            <span class="material-symbols-outlined">arrow_forward</span>
        </button>
    </div>
</template>