<script setup>
import { ref, defineProps, watch } from 'vue';
const props = defineProps({
    national: Object
})
const url = ref('')
watch(
  () => props.national,
  () => {
    url.value = `https://www.google.com/maps/place/${props.national.countryName}`
  }
)

</script>
<template>
    <div>
        <iframe :src="url" width="100%" height="350px"></iframe>
        {{ props.national }}
        {{ props.national.countryName }}
    </div>
</template>