
<script setup>
import axios from 'axios'
import { ref } from 'vue';
import Search from './components/Search.vue'
import Detail from './components/Detail.vue'

const national = ref({})
const searchWord = ref('')
const openSearch = ref(true)

const emitSearchText = (val) => {
  const regex = /^[ㄱ-ㅎ가-힣]+$/
  if (!regex.test(val)) {
    window.alert('한글로 입력해주세요.')
    return
  }
  if (val === searchWord.value) {
    return
  }
  searchWord.value = val
  loadData(val)
}
const loadData = async(name) => {
  try {
    const serviceKey = '%2B4pVJGaV31U%2Bt9T20cC9ohs%2BZD%2FmqBa4%2FypH8N21OphUUs0ErGEEFJGaiHzEx632g7YprcB9UqPGFT7ygJ6XMQ%3D%3D'
    const url = `http://apis.data.go.kr/1262000/CountryBasicService/getCountryBasicList?ServiceKey=${serviceKey}&countryName=${name}`
    const response = await axios.get(url)
    const data = response.data.response.body.items.item
    if (data) {
      national.value = JSON.parse(JSON.stringify(data))
      openSearch.value = false
      // console.log(national.value)
    } else {
      openSearch.value = true
      window.alert('국가명을 확인해주세요.')
    }
  } catch (err) {
    console.error(err)
  }
}
</script>
<template>
  <img :class="openSearch ? 'on' : 'off'" src="./assets/wayout.svg" alt="wayout logo">
  <div :class="openSearch ? 'on' : 'off'" class="text-info">
    <h3>여행지 정보 검색 서비스</h3>
    <ul>
      <li>*국가명은 한글로 입력해주세요.</li>
      <li>*대한민국, 북한은 검색 제외 대상입니다.</li>
    </ul>
  </div>
  <Search :class="openSearch ? 'on' : 'off'" @emitSearchText="emitSearchText" />
  <Detail :national="national" />
</template>
